import React from 'react';
import axios from 'axios';
import {parseString} from 'xml2js';
import BooksPage from '../booksgridview/BooksPage';
import {Link} from 'react-router-dom';
class SearchBar extends React.Component{

   constructor(props){
       super(props)
       this.state={
           term:'',
           value:''
       }
       this.handleChange=this.handleChange.bind(this);
   }
   handleChange(e){
    this.setState({value:e.target.value})
   }
         
     render(){
     return(<div><form className="form-inline my-2 my-lg-0" onSubmit={this.props.handleSubmit}>
     <input className="form-control mr-sm-2" type="search" placeholder="Enter Author or Title" aria-label="Search" name="search" value={this.state.value} onChange={this.handleChange}/>
     <button className="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
        
   </form>
   
   </div>
   );}
 };

 export default SearchBar;


