import React from 'react';
import BookList from './BookList';
import axios from 'axios'
import {parseString} from 'xml2js';
class BooksPage extends React.Component{
    constructor(props){
        super(props);
        this.state={
            books:[]
        }
    }
   
    render(){
        return(
        <div className="row">
        
        <BookList books={this.props.books} /> 
        </div>)
    }
}
export default BooksPage


